@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;0,400;1,300;1,400&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  
  font-family: 'Cormorant Garamond';
}

body{
  background-image: url('../images/bg_normal.jpg');
  font-family: var(--font-NeueMachina),ui-sans-serif,system-ui,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
}

.wrapper{
  display: flex;
  flex-direction: column;
}

.main-area{
  width: 45%;
  height: 100vh;
  margin-left: 30%;
}

.header {
  color: #222;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;;
  padding: 10px 0;
  width: 100%;
  height: 8vh;
  top: 0;
  left: 0;
  position: fixed;
  background-image: url('../images/herringbone.webp');
}

ul {
  list-style: none;
}

.navigation{
  display: flex;
  flex-direction: row;
  justify-content: space-around; /* center items horizontally */
  align-items: center; /* center items vertically */
}

.navigation ul li {
  border: 1px solid black;
  display: inline-block;
  padding: .7rem .9rem;
  text-decoration: none;
  transition: transform 0.2s ease-in-out;
  box-shadow: inset 0 0 10px #000000;
  border-radius: 5px;
}
.navigation ul li:hover {
  background-color: #59595948;
  color: #000000ef;
  transform: scale(0.95);
  cursor: default;
}

.container-header {
  width: 100%;
  padding-right: var(--bs-gutter-x,.75rem);
  padding-left: var(--bs-gutter-x,.75rem);
  margin-right: auto;
  margin-left: auto;
}



/*test area*/

.container
{
  margin-top: 10vh;
}
.container .hero {
  display: grid;
  grid-template-columns: 60% 40%;
  height: 100vh;
}
.container .hero .copy {
  display: grid;
  align-content: center;
  padding: 0 1rem;
  height: 100%;
}
.container .hero .copy blockquote {
  font-size: 6vmin;
  font-weight: 300;
}
.container .hero .copy blockquote span {
  font-style: italic;
  font-weight: 400;
}
.container .hero .copy blockquote cite {
  font-weight: 300;
  font-style: italic;
  font-size: 4.5vmin;
  display: block;
  padding: 8px 0;
}
.container .hero .image-wrapper {
  height: 100vh;
  width: 100%;
}
.container .hero .image-wrapper img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.container .section-0 {
  margin-top: 20vh;
  text-align: center;
}
.container .section-0 p {
  font-size: 5vw;
}
.container .section-0 cite {
  font-size: 3vw;
}
.container .section-1 {
  height: 100vh;
  display: grid;
  place-content: center;
  padding: 0 1rem;
}
.container .section-1 .image-mask {
  width: 80vw;
  height: 80vh;
  background: url("https://firebasestorage.googleapis.com/v0/b/hashnode-tutorials.appspot.com/o/bwimages%2Ftumblr_n92tblyC8d1s3sziio1_1280.jpg?alt=media&token=c5d75884-4bc3-46bb-815d-414d2ec68e4b");
  background-size: cover;
  background-position-x: 50%;
  background-position-y: 90%;
  background-size: 100%;
}
.container .section-1 .image-mask img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.container .section-1 blockquote {
  font-size: 9vmin;
  font-weight: 300;
}
.container .section-1 blockquote span {
  font-style: italic;
  font-weight: 400;
}
.container .section-1 blockquote cite {
  font-weight: 300;
  font-style: italic;
  font-size: 7.5vmin;
  display: block;
  padding: 8px 0;
}
.container .section-2 {
  height: calc(300vh + 32px + 16px);
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-columns: 40% 60%;
}
.container .section-2 blockquote {
  font-size: 5vmin;
  padding: 16px 16px 0 16px;
}
.container .section-2 blockquote cite {
  display: block;
}
.container .section-2 .images-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 0 8px;
  gap: 8px;
  margin-top: 16px;
}
.container .section-2 .images-container img {
  width: 100%;
  max-height: 75vh;
  height: 100%;
  align-self: stretch;
  object-fit: cover;
}
.container .section-3 {
  height: 100vh;
  display: grid;
  grid-template-columns: 40% 60%;
  position: relative;
}
.container .section-3 .image-wrapper {
  height: 100%;
  width: 100%;
}
.container .section-3 .image-wrapper img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.container .section-3 .copy {
  font-size: 8vmin;
  height: 100vh;
  width: 100%;
  display: grid;
  align-content: center;
}
.container .section-3 .copy blockquote {
  margin: 0 32px;
}
.container .section-3 .copyright {
  position: absolute;
  bottom: 5%;
  left: 50%;
  font-size: 4vmin !important;
}